.recipieCard {
  display: inline-block;
  &__container {
    background-size: cover;
    padding: 10px;
    background-repeat: no-repeat;
    width: 257px;
    margin: 20px 5px 0 5px;
    height: 217px;
    display: inline-block;
    background-color: black;
    cursor: pointer;
    border-radius: 5px;
  }
  &__container:hover {
    opacity: 0.6;
  }

  &__title {
    text-align: center;
    text-transform: capitalize;
    font-weight: 300;
    font-size: 20px;
  }
}
